export default {
	data() {
		return {
			shopid: 0,
			name: '',
			people: ''
		}
	},
	onLoad: function(option) {
		this.shopid = option.id
	},
	methods: {
		addTypes: function() {
			if (this.name) {
				uni.request({
					url: this.$bashUrl + '/index.php/home/Api/type_mange',
					method: 'POST',
					data: {
						type: 'add',
						store_id: this.shopid,
						name: this.name
					},
					success: (res => {
						console.log(res)
						uni.showToast({
							title: '添加类型成功'
						});
						setTimeout(function() {
							uni.navigateBack({
								delta: 1
							})
						}, 1000)
					}),
					fail: (err => {
						uni.showToast({
							title: '未添加成功',
							icon: 'none'
						});
					})
				})
			}else{
				uni.showToast({
					title:"请输入类名",
					icon:'none'
				})
			}
		}
	}
}
